<!--
 * @Descripttion: 科室维护列表
 * @version: 
 * @Author: 曾元球
 * @Date: 2019-06-20 09:05:19
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 17:58:33
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">科室维护</div>
      <div class="head-btn"
        v-if="btns.hasInsert"
        data-btn-id='106002001'
        @click="openDialog('insert')">
        <img src="@/assets/img/add.png"
          alt="新增">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='所属机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>

        <el-form-item label='科室名称：'>
          <el-input placeholder="请输入"
            v-model="querys.dname"
            clearable />
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>

        <el-form-item label="显示状态："
          style="float:right;margin-right:0">
          <el-radio-group class="red"
            v-model="querys.enabled"
            @change="$refs.pagination.initPageList(1)">
            <el-radio label="">全部</el-radio>
            <el-radio label="1">已启用</el-radio>
            <el-radio label="2">已禁用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='scope'>
            {{scope.$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <!--  min-width='100' -->
        <el-table-column label='所属机构'
          prop='oname'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='科室编号'
          prop='sysGuid'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='科室名称'
          prop='dname'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='科室状态'
          width="80">
          <template v-slot="scope">
            <span :style="{color: scope.row.enabled == 1 ? '#2ecc71':'#fe4d19'}">
              {{scope.row.enabled == 1 ? '已启用' : '已禁用'}}
            </span>
          </template>
        </el-table-column>
        <el-table-column label='创建人员'
          prop='createUserName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='创建日期'
          width="120">
          <template v-slot="scope">
            {{scope.row.createDate | formatDate}}
          </template>
        </el-table-column>

        <el-table-column label='操作'
          width="120">
          <template v-slot="{row}">
            <i class="iconfont bj-search"
              @click="openDialog('see', row)"></i>
            <i class="iconfont bj-edit"
              v-if="handleBtnShow(row, 'hasUpdate')"
              data-btn-id='106002002'
              @click="openDialog('update', row)"></i>
            <i class="iconfont bj-delete"
              v-if="handleBtnShow(row, 'hasDel')"
              data-btn-id='106002004'
              @click="delConfirm(row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination queryUrl='/sys/dept/listPaging'
        :queryObj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination' />
    </div>

    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='500px'
      :title="dialogTitles[dialogType]"
      :close-on-click-modal='false'
      center>
      <template v-if='dialogType==="see"'>
        <div class="flex">
          <el-form size="small"
            class="see-style"
            label-width="auto">
            <el-form-item label="科室编号：">
              {{dataInfo.sysGuid}}
            </el-form-item>
            <el-form-item label="机构名称：">
              {{dataInfo.oname}}
            </el-form-item>
            <el-form-item label="类别名称：">
              {{deptCategoryName}}
            </el-form-item>
            <el-form-item label="科室顺序：">
              {{dataInfo.sortNum}}
            </el-form-item>
            <el-form-item label="科室名称：">
              {{dataInfo.dname}}
            </el-form-item>
            <el-form-item label="拼音简码：">
              {{dataInfo.pySimpleCode}}
            </el-form-item>
            <el-form-item label="五笔简码：">
              {{dataInfo.wbSimpleCode | emptyData}}
            </el-form-item>
            <el-form-item label="状态：">
              {{dataInfo.enabled == 1 ? '启用' : '禁用'}}
            </el-form-item>
          </el-form>
        </div>
        <div slot='footer'
          v-if="btns.hasUpdate"
          data-btn-id='106002002'>
          <el-button class="btn-info"
            @click="openDialog('update')">修改</el-button>
        </div>
      </template>
      <template v-else>
        <el-form size="small"
          label-width="auto"
          :model="formFields"
          :rules="rules"
          ref="form">
          <el-form-item label="机构名称："
            prop="organizationGuid">
            <el-select class="block"
              v-model="formFields.organizationGuid">
              <el-option v-for="item in orgList"
                :key='item.sysGuid'
                :value="item.sysGuid"
                :label="item.oname" />
            </el-select>
          </el-form-item>

          <el-form-item label="类别名称："
            prop="deptCaty">
            <el-select class="block"
              v-model="formFields.deptCaty">
              <el-option v-for='item in categoryList'
                :key='item.sysGuid'
                :value="item.sysGuid"
                :label="item.deptTypeName"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="科室名称："
            prop="dname">
            <el-input placeholder="请输入"
              v-model="formFields.dname"
              :disabled="dialogType === 'update'"
              maxlength="51"></el-input>
          </el-form-item>
          <el-form-item label="科室顺序："
            prop="sortNum">
            <el-input placeholder="请输入"
              :value="formFields.sortNum"
              @input="$outInt($event, 'sortNum', formFields)"
              maxlength="5"></el-input>
          </el-form-item>

          <el-form-item label="拼音简码："
            prop="pySimpleCode">
            <el-input placeholder="请输入"
              v-model="formFields.pySimpleCode"
              maxlength="51"></el-input>
          </el-form-item>
          <el-form-item label="五笔简码："
            prop="wbSimpleCode">
            <el-input placeholder="请输入"
              v-model="formFields.wbSimpleCode"
              maxlength="51"></el-input>
          </el-form-item>
          <el-form-item label="状态："
            prop="enabled">
            <el-select class="block"
              v-model="formFields.enabled">
              <el-option value="1"
                label="启用"></el-option>
              <el-option value="2"
                label="禁用"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-hollowing"
            @click="dialogIsShow = false">取消</el-button>
          <el-button class="btn-info"
            @click="onSubmit"
            :loading="submitLoading">保存</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { lenRule } from '@/lib/utils'
import { mapState } from 'vuex'
export default {
  name: 'DepartmentList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      querys: {
        dname: '',
        enabled: '',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,

      // orgList: [],
      categoryList: [],

      formFields: {
        organizationGuid: '', // 所属机构Id
        sysGuid: '', //	string	是
        dname: '', //	string	是	科室名称
        deptCaty: '', //	string	是	科室类别1实验室检查,2医技检查,3科室检查
        sortNum: '', //	string	是	排序
        pySimpleCode: '', //	string	是	拼音简码
        wbSimpleCode: '', //	string	是	五笔简码
        enabled: '1'
      },

      rules: {
        organizationGuid: { required: true, message: '请选择所属机构' },
        dname: [
          {
            required: true,
            message: '请输入科室名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        deptCaty: { required: true, message: '请选择科室类别' },
        sortNum: { required: true, message: '请输入科室顺序', trigger: 'blur' },
        pySimpleCode: [
          {
            required: true,
            message: '请输入拼音简码',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        wbSimpleCode: { max: 50, message: '最多输入50个字符' }
      },
      dataInfo: {},
      dialogIsShow: false,
      dialogType: '',
      loading: true,
      submitLoading: false
    }
  },

  computed: {
    deptCategoryName() {
      if (!this.formFields.deptCaty || !this.categoryList.length) return ''
      return this.categoryList.find(item => item.sysGuid === this.formFields.deptCaty).deptTypeName
    },
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106002001'), // 登记按钮权限
        hasUpdate: ~btnIds.indexOf('106002002'), // 修改按钮权限
        hasStatus: ~btnIds.indexOf('106002003'), // 更改状态权限
        hasDel: ~btnIds.indexOf('106002004') // 删除按钮权限
      }
    },
    ...mapState(['orgList'])
  },

  created() {
    this.initStatic()
  },

  methods: {
    initStatic() {
      this.dialogTitles = { see: '查看', insert: '新增', update: '修改' }
    },

    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    handleBtnShow(row, btnType) {
      let { organizationGuid, enabled } = row,
        isCurrentOrg = organizationGuid === this.$store.getters.o_userinfo.orgGuid,
        isEnabled = enabled == 1
      return isCurrentOrg && isEnabled && this.btns[btnType]
    },

    delConfirm({ sysGuid }) {
      this.$confirm('', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/sys/dept/remove', { sysGuid })
            .then(res => {
              this.$message.success('删除成功')
              let pagination = this.$refs.pagination
              if (this.tableData.length > 1) return pagination.initPageList()
              let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
              pagination.initPageList(pageNum)
            })
            .catch(err => {
              console.log(err)
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },

    openDialog(type, data) {
      this.getAllCategory()
      this.dialogIsShow = true
      this.dialogType = type
      if (data) {
        this.dataInfo = data
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = data[key]
        })
        this.formFields.sysGuid = data.sysGuid
      } else if (type === 'insert') {
        delete this.formFields.sysGuid
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = ''
        })
        this.formFields.enabled = '1'
      }
      this.$nextTick(_ => {
        this.dialogType !== 'see' && this.$refs.form.clearValidate()
      })
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        let url = `/sys/dept/${this.dialogType}`
        let msg = this.dialogTitles[this.dialogType]
        this.$post(url, this.formFields)
          .then(res => {
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },

    // 获取类别
    getAllCategory() {
      if (this.categoryList.length) return
      this.$post('/sys/deptType/listPaging', {
        pageNum: '1',
        pageSize: '10000',
        enabled: '1'
      })
        .then(({ data }) => {
          if (data) this.categoryList = data.list
        })
        .catch(err => this.$message.error(err))
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}

.table-box {
  padding: 0 20px;
  overflow: hidden;
}

.iconfont {
  color: @color-green;
  cursor: pointer;
}

.see-style {
  margin: 0 auto;
  max-width: 100%;
}
</style>



